.mywork{
    padding: 0 80px;
    gap: 16px;
    justify-content: space-between;
}
.mywork h1{
    width: 100%;
}
.p-card{
    height: 200px;
    width: 300px;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    
}
.p-card img{
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-user-drag: none;
}
.p-view{
    position: absolute;
     background-color: #ff0051ea;
    width: 100%;
    height: 0;
    padding: 0;
    margin: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: 700;
    transition: 0.5s ease-in-out;
    overflow: hidden;
}
.p-card h3{
    width: 100%;
    text-align: center;
    position: absolute;
    padding: 16px;
    background: linear-gradient(#000000,transparent);
}
@media only screen and (max-width: 600px) {
    .mywork{
        padding: 18px;
    }
}